import React from 'react';
import { Link, Routes, Route } from 'react-router-dom';
import NewsCard from './NewsCard';
import NewsList from './NewsList';

import NewsData from '../../data/NewsData';

const News = () => {
    return (
        <div>
            <h2>News</h2>

            <nav>
                <Link to='/news/news_card'>카드형</Link>&nbsp;|&nbsp;
                <Link to='/news/news_list'>리스트형</Link>
            </nav>

            <Routes>
                {/* Route에 연결된 컴포넌트에 props 전달 */}
                <Route path='news_card' element={<NewsCard news={NewsData} />} />
                <Route path='news_list' element={<NewsList news={NewsData} />} />
            </Routes>
        </div>
    );
};

export default News;